import * as React from "react";
import { useState, useEffect } from "react";
import { Input, Button } from "@material-tailwind/react";
import { FiSearch } from "react-icons/fi";
import { IoClose, IoSearch } from "react-icons/io5";

type T_SearchBar = {
  className?: string;
  setPayload?: (payload: any) => void;
  loadingState?: boolean;
};

const SearchBar: React.FC<T_SearchBar> = ({ className, setPayload, loadingState }) => {
  const [input, setInput] = useState("");

  useEffect(() => {
    if (input.length === 0) {
      setDefault();
    }
  }, [input]);

  const setDefault = () => {
    setInput("");
    // setPayload((prev) => ({ ...prev, search: "" }));
  };

  const onSearch = () => {
    if (input.length > 0) {
      // setPayload((prev) => ({ ...prev, search: input }));
    }
  };

  return (
    <div className={"flex flex-1 items-center " + className}>
      <div className="flex flex-1 pl-4 gap-2 rounded-lg rounded-r-none !border !border-gray-300 bg-white items-center focus:!border-blue-500 focus:!border-t-blue-500">
        <IoSearch size={20} className="text-black" />
        <Input
          placeholder="Search data here"
          // size="lg"
          icon={input.length > 0 && <IoClose size={20} className="cursor-pointer hover:text-blue-700" onClick={setDefault}/>}
          className="h-full !border-none text-gray-900 ring-4 ring-transparent placeholder:text-gray-500 placeholder:opacity-100 focus:!border-blue-500 focus:!border-t-blue-500"
          labelProps={{
            className: "hidden",
          }}
          value={input}
          onChange={(e) => setInput(e.target.value)}
        />
      </div>

      <Button
        size="md"
        loading={loadingState !== undefined && loadingState}
        className="flex px-4 normal-case bg-blue-50 rounded-l-none text-black items-center justify-center"
        color="blue"
        onClick={onSearch}
        disabled={input.length === 0}
      >
        Search
      </Button>
    </div>
  );
};

export default SearchBar;
