import * as React from 'react';
import { useState } from 'react';
import { Input, Popover, PopoverContent, PopoverHandler, Typography } from '@material-tailwind/react';
import { DayPicker } from "react-day-picker";
import { format, set } from "date-fns";
import { LuCalendarDays } from "react-icons/lu";

const Header = () => {
  const [date, setDate] = useState<Date>(new Date());

  return (
    <div className='flex px-4 py-2 gap-4 bg-gray-50 items-center justify-between'>
      <div className='flex gap-2 items-center'>
        <Typography className="px-2 text-xs text-primary-500 font-normal bg-primary-50 rounded-full border border-primary-500">
          Submitted: 35.000
        </Typography>
        <Typography className="px-2 text-xs text-black font-normal bg-white rounded-full">
          Screening: <span className='font-bold'>100</span>
        </Typography>
        <Typography className="px-2 text-xs text-black font-normal bg-white rounded-full">
          QC: <span className='font-bold'>4.400</span>
        </Typography>
      </div>

      <div className="w-fit">
        <Popover placement="bottom">
          <PopoverHandler>
            <Input
              icon={<LuCalendarDays size={20} className='text-gray-500'/>}
              labelProps={{ className: "hidden" }}
              size="lg"
              id="from_date"
              type="text"
              placeholder="Select distribute date"
              className="!border !border-gray-300 bg-white text-gray-900 ring-4 ring-transparent placeholder:text-gray-500 placeholder:opacity-100 focus:!border-primary-500 focus:!border-t-primary-500"
              containerProps={{ className: "min-w-[8px] w-fit" }}
              required={true}
              value={format(date, "dd/MM/yyyy")}
              // onChange={(e) => setInputs((prev) => ({...prev, [item.field_name]: e.target.value}))}
              // disabled={formDisabled || loadingStates.form}
            />
          </PopoverHandler>
          <PopoverContent>
            <DayPicker
              captionLayout="dropdown"
              mode="single"
              selected={date}
              onSelect={(date) => setDate(date)}
              classNames={{
                today: `text-primary-500`,
                selected: `border-2 border-primary-500 rounded-full font-bold text-primary-500`,
                chevron: `fill-primary-500`
              }}
            />
          </PopoverContent>
        </Popover>
      </div>
    </div>
  )
}

export default Header