import * as React from 'react';
import { useState } from 'react';
import { Button, Card, CardHeader, CardBody, CardFooter, Accordion, AccordionHeader, AccordionBody, Radio, Typography } from '@material-tailwind/react';
import Select from 'react-select';
import { DocumentIcon } from '../../assets/icon/index';
import { IoIosArrowDown } from "react-icons/io";
import Anime from '../../assets/image/sample.png';
import Anime1 from '../../assets/image/sample1.jpg';
import Anime2 from '../../assets/image/sample2.jpg';
import Anime3 from '../../assets/image/sample3.jpg';
import Anime4 from '../../assets/image/sample4.jpg';
import Anime5 from '../../assets/image/sample5.jpg';
import Anime6 from '../../assets/image/sample6.jpg';

type T_Option = {
  value: string;
  label: string;
}

const ProctoringTab = () => {
  const [fraudType, setFraudType] = useState<T_Option>(null);
  const [status, setStatus] = useState<'isFraud' | 'isNotFraud'>('isNotFraud');
  
  return (
    <div className='flex flex-col gap-10 bg-white rounded-md'>
      <ProctoringSection testType='Online Test 1'/>
      <hr />
      <ProctoringSection testType='Online Test 2'/>

      <div className='flex flex-col p-4 gap-2'>
        <Typography className="mb-2 text-black font-bold">
          Validasi Hasil Proctoring Peserta
        </Typography>

        <Radio
          name="status" 
          label={
            <Typography className="text-danger-500 font-bold">
              Kategori Fraud <span className='text-black font-normal'>(Melakukan Kecurangan)</span>
            </Typography>
          }
          labelProps={{ className: "-ml-1" }}
          checked={status === 'isFraud'}
          onChange={() => setStatus('isFraud')}
        />
        <Select
          placeholder="Choose"
          required={true}
          className="w-full max-w-[300px] ml-[40px]"
          isDisabled={status !== 'isFraud'}
          components={{
            IndicatorSeparator: () => null
          }}
          options={[
            {value: "all", label: "All"},
            {value: "multiface", label: "Multiface"},
            {value: "hp", label: "Menggunakan HP"},
            {value: "masker", label: "Menggunakan Masker"},
            {value: "wajah_berbeda", label: "Wajah Berbeda"}
          ]}
          styles={{
            menuList: (base) => ({
              ...base,
              color: "black",
              fontWeight: "normal",
            }),
            control: (base) => ({
              ...base,
              height: "44px",
              color: "black",
              fontWeight: "normal",
              fontSize: "14px",
            }),
          }}
          menuPlacement='auto'
          value={fraudType}
          onChange={(e) => setFraudType(e)}
        />
        <Radio
          name="status" 
          label={
            <Typography className="text-success-600 font-bold">
              Kategori Not Fraud <span className='text-black font-normal'>(Tidak Melakukan Kecurangan)</span>
            </Typography>
          }
          labelProps={{ className: "-ml-1" }}
          checked={status === 'isNotFraud'}
          onChange={() => setStatus('isNotFraud')}
        />
      </div>

      <div className='flex p-4 gap-2 border-t items-center justify-end'>
        <Button
          variant="outlined"
          color="blue"
          size="sm"
          className="normal-case text-base text-primary-500 border-primary-500"
        >
          Back
        </Button>
        <Button
          variant="filled"
          color="blue"
          size="sm"
          className="normal-case text-base bg-primary-500"
        >
          Submit Proctoring
        </Button>
      </div>
    </div>
  )
}

type T_ProctoringSection = {
  testType: string;
}

const ProctoringSection: React.FC<T_ProctoringSection> = ({testType}) => {
  const [tabOpen, setTabOpen] = useState<boolean>(true);
  
  const proctoringFace = [
    {date: '2024-05-28 13:04:59', image: Anime2},
    {date: '2024-05-28 13:04:59', image: Anime2},
    {date: '2024-05-28 13:04:59', image: Anime2},
    {date: '2024-05-28 13:04:59', image: Anime2},
    {date: '2024-05-28 13:04:59', image: Anime2},
    {date: '2024-05-28 13:04:59', image: Anime2},
    {date: '2024-05-28 13:04:59', image: Anime2},
    {date: '2024-05-28 13:04:59', image: Anime2},
    {date: '2024-05-28 13:04:59', image: Anime2},
  ]
  
  return (
    <div className='flex flex-col p-4 gap-4'>
      <div className='flex gap-4 items-center justify-between'>
        <div className='grid grid-cols-6 flex-1 gap-2 gap-y-0'>
          <Typography className='col-span-1 text-sm text-black font-bold'>Email:</Typography>
          <Typography className='col-span-5 text-sm text-black font-normal'>Malvinocgv@gmail.com</Typography>
          
          <Typography className='col-span-1 text-sm text-black font-bold'>Test Type:</Typography>
          <Typography className='col-span-5 text-sm text-black font-normal'>{testType}</Typography>

          <Typography className='col-span-1 text-sm text-black font-bold'>Date Test:</Typography>
          <Typography className='col-span-5 text-sm text-black font-normal'>3 Agustus 2024 13:00:00 - 15:15:00</Typography>
        </div>

        <Typography className="px-4 py-1 text-sm text-success-700 font-bold bg-success-100 rounded-full">Detected Status</Typography>
      </div>

      <hr />

      <div className='flex flex-col gap-4'>
        <div className='flex gap-4 items-center'>
          <Card className="p-0 rounded-md overflow-hidden">
            <CardHeader
              shadow={false} 
              floated={false}
              className="flex p-4 m-0 gap-2 bg-gray-50 rounded-none items-center"
            >
              <DocumentIcon/>
              <Typography className='text-gray-500 text-sm font-bold tracking-widest'>FOTO PROFILE</Typography>
            </CardHeader>
            <CardBody className="max-h-[200px] max-w-[200px] p-0">
              <img src={Anime1} alt="Anime1" className="h-[200px] object-cover object-center"/>
            </CardBody>
            <CardFooter className="flex p-2 gap-2 bg-gray-50 items-center">
              <Typography className='text-gray-500 text-xs font-normal'>2024-05-28 13:04:59</Typography>
            </CardFooter>
          </Card>

          <Card className="p-0 rounded-md overflow-hidden">
            <CardHeader
              shadow={false} 
              floated={false}
              className="flex p-4 m-0 gap-2 bg-gray-50 rounded-none items-center"
            >
              <DocumentIcon/>
              <Typography className='text-gray-500 text-sm font-bold tracking-widest'>FOTO TERDAFTAR SAAT TEST</Typography>
            </CardHeader>
            <CardBody className="max-h-[200px] p-0">
              <img src={Anime} alt="Anime" className="h-[200px] object-cover"/>
            </CardBody>
            <CardFooter className="flex p-2 gap-2 bg-gray-50 items-center">
              <Typography className='text-gray-500 text-xs font-normal'>2024-05-28 13:04:59</Typography>
            </CardFooter>
          </Card>
        </div>

        <Accordion
          open={tabOpen}
          className="flex flex-col p-0 rounded-md border shadow-md overflow-hidden"
          icon={<IoIosArrowDown size={20} className={`text-black transition-transform ${tabOpen ? "rotate-180" : "rotate-0"}`}/>}
        >
          <AccordionHeader
            className="p-4 bg-gray-50 rounded-none"
            onClick={() => setTabOpen((prev) => !prev)}
          >
            <div className='flex gap-2 items-center'>
              <DocumentIcon/>
              <Typography className='text-gray-500 text-sm font-bold tracking-widest'>PROCTORING (FACE)</Typography>
            </div>
          </AccordionHeader>
          <AccordionBody className="grid grid-cols-3 p-4 gap-4">
            {proctoringFace.map((item, index) => (
              <Card key={index} className="col-span-1 p-0 rounded-md overflow-hidden">
                <CardBody className="max-h-[200px] p-0">
                  <img src={item.image} alt={"Anime" + index} className="h-full w-full object-cover object-center"/>
                </CardBody>
                <CardFooter className="flex p-2 gap-2 bg-gray-50 items-center">
                  <Typography className='text-gray-500 text-xs font-normal'>{item.date}</Typography>
                </CardFooter>
              </Card>
            ))}
          </AccordionBody>
        </Accordion>
      </div>
    </div>
  )
}

export default ProctoringTab